<template>
  <div id="map"></div>
</template>

<script>
import "mapbox-gl/dist/mapbox-gl.css";
import mapboxgl from "mapbox-gl";

const mapStyle = [
  "mapbox://styles/mapbox/streets-v11", //Mapbox Streets
  "mapbox://styles/mapbox/outdoors-v11", //Mapbox Outdoors
  "mapbox://styles/mapbox/light-v10", //Mapbox Light
  "mapbox://styles/mapbox/dark-v10", //Mapbox Dark
  "mapbox://styles/mapbox/satellite-v9", //Mapbox Satellite
  "mapbox://styles/mapbox/satellite-streets-v11", //Mapbox Satellite Streets
  "mapbox://styles/mapbox/navigation-day-v1", //Mapbox Navigation Day
  "mapbox://styles/mapbox/navigation-night-v1", //Mapbox Navigation Night

  "mapbox://styles/jamestim9215/ckvkdj5cd1q4115nzwxa9rny3", //客製化
];

import parkingInfo from "@/assets/json/parkingInfo.json";

export default {
  props: {
    carType: Number,
    getLngLat: Boolean,
    city: String,
    cityArea: String,
  },
  data() {
    return {
      map: null,
      accessToken:
        "pk.eyJ1IjoiamFtZXN0aW05MjE1IiwiYSI6ImNrdmo2Y2I4aDVmejEzMm1zbThsdGdoajAifQ.UfBJAI7fFsv2wcAEBo6WUA",
      bounds: [
        [119.3620389067818, 21.83282743468497],
        [122.07013958372167, 25.34391086143183],
      ],
      center: [121.5173399, 25.0475613],
      currentMarkers: [],

      lngLatMaker: null,
    };
  },
  mounted() {
    this.mapBoxInitial();
  },
  watch: {
    carType() {
      // console.log(this.map.transform.center);
      this.setMaker();
    },
    city() {
      this.setMaker();
    },
    cityArea() {
      this.setMaker();
    },
    getLngLat() {
      if (this.lngLatMaker !== null) {
        this.lngLatMaker.remove();
        const coordinates = document.getElementById("coordinates");
        coordinates.style.display = "none";
      }
      if (this.getLngLat == true) {
        this.setLngLatMaker();
      }
    },
  },
  methods: {
    isMatchCarType(type, list) {
      var isMatch = false;
      for (var key in list) {
        if (list[key] == type) {
          isMatch = true;
          break;
        }
      }
      return isMatch;
    },
    isMatchArea(address) {
      var isMatch = false;
      if (address.match(this.city) && address.match(this.cityArea)) {
        isMatch = true;
      }
      return isMatch;
    },
    isMatchLngLat(coordinates) {
      // console.log("["+this.map.getBounds()._sw.lng+" , "+coordinates[0]+" , "+this.map.getBounds()._ne.lng+"]");
      // console.log("["+this.map.getBounds()._sw.lat+" , "+coordinates[1]+" , "+this.map.getBounds()._ne.lat+"]");
      var isMatch = false;
      if (this.map.transform) {
        if (
          this.map.getBounds()._sw.lng < coordinates[0] &&
          this.map.getBounds()._ne.lng > coordinates[0] &&
          this.map.getBounds()._sw.lat < coordinates[1] &&
          this.map.getBounds()._ne.lat > coordinates[1]
        ) {
          isMatch = true;
        }
      }
      return isMatch;
    },
    mapBoxInitial() {
      mapboxgl.accessToken = this.accessToken;
      this.map = new mapboxgl.Map({
        container: "map", // container ID
        style: mapStyle[8], // style URL
        center: this.center, // starting position [lng, lat]
        // center: [121.54339, 24.978672],
        // zoom: 16, // starting zoom
        zoom: 14, // starting zoom
        // bearing: 0,
        // pitch: 60,
        trackResize: true,
        maxBounds: this.bounds,
      });

      // this.map.on("resize", () => {
      //   this.setMaker();
      // });
      this.map.on("dragend", () => {
        this.setMaker();
      });
      this.map.on("zoomend", () => {
        
        
        this.setMaker();
      });

      // Add geolocate control to the map.
      this.map.addControl(
        new mapboxgl.GeolocateControl({
          positionOptions: {
            enableHighAccuracy: true,
          },
          // When active the map will receive updates to the device's location as it changes.
          trackUserLocation: true,
          // Draw an arrow next to the location dot to indicate which direction the device is heading.
          showUserHeading: true,
        })
      );

      this.setMaker();
    },
    setMaker() {
      if (this.currentMarkers !== null) {
        for (var i = this.currentMarkers.length - 1; i >= 0; i--) {
          this.currentMarkers[i].remove();
        }
      }
      // Add markers to the map.
      for (const marker of parkingInfo.features) {
        if (
          this.isMatchCarType(this.carType, marker.carType) &&
          this.isMatchArea(marker.properties.address) &&
          this.isMatchLngLat(marker.geometry.coordinates)
        ) {
          // Create a DOM element for each marker.
          const el = document.createElement("div");
          const width = marker.properties.iconSize[0];
          const height = marker.properties.iconSize[1];
          el.className = "marker";
          el.style.width = `${width}px`;
          el.style.height = `${height}px`;
          // el.style.visibility = '';

          const tag = document.createElement("div");
          tag.className = "tag";
          el.append(tag);
          const img = document.createElement("img");
          img.className = "img";
          img.src = `${marker.imgUrl}`;
          el.append(img);

          el.addEventListener("click", () => {
            this.map.flyTo({
              // These options control the ending camera position: centered at
              // the target, at zoom level 9, and north up.
              center: marker.geometry.coordinates,
              zoom: 18,
              bearing: 0,

              // These options control the flight curve, making it move
              // slowly and zoom out almost completely before starting
              // to pan.
              speed: 1, // make the flying slow
              curve: 1, // change the speed at which it zooms out

              // This can be any easing function: it takes a number between
              // 0 and 1 and returns another number between 0 and 1.
              easing: (t) => t,

              // this animation is considered essential with respect to prefers-reduced-motion
              essential: true,
            });
            var data = marker.properties;
            this.$emit('parkingInfo', data);
            // window.alert(marker.properties.info);
          });

          const popup = new mapboxgl.Popup({ offset: 25 }).setText(
            marker.properties.info
          );

          // Add markers to the map.
          var oneMarker = new mapboxgl.Marker(el)
            .setLngLat(marker.geometry.coordinates)
            .setPopup(popup) // sets a popup on this marker
            .addTo(this.map);
          this.currentMarkers.push(oneMarker);
        }
      }
    },
    onDragEnd() {
      const coordinates = document.getElementById("coordinates");
      const lngLat = this.lngLatMaker.getLngLat();
      coordinates.style.display = "block";
      coordinates.innerHTML = `[Longitude, Latitude]<br />[${lngLat.lng}, ${lngLat.lat}]`;
    },
    setLngLatMaker() {
      this.lngLatMaker = new mapboxgl.Marker({
        draggable: true,
        color: "#2ee7d6",
      })
        .setLngLat(this.map.getCenter())
        .addTo(this.map);

      this.onDragEnd();

      this.lngLatMaker.on("dragend", this.onDragEnd);
    },
  },
};
</script>

