<template>
  <div id="app">
    <div class="window-box-cover" v-if="windowOpen">
      <div class="window-box">
        <h3 style="margin-bottom: 15px">嗨! 請選擇你要找的車位種類!</h3>
        <select name="" id="initSelectType">
          <option
            :value="key.type"
            v-for="(key, index) in carType"
            :key="index"
          >
            {{ key.name }}
          </option>
        </select>
        <button class="btn" @click="initCarTypeChange">確定</button>
      </div>
    </div>

    <div class="appTitle">
      <h1>
        我到底能停哪?
      </h1>
    </div>
    <button class="btn btnGetLngLat" @click="getLngLat?getLngLat=false:getLngLat=true" :class="getLngLat?'active':''">{{getLngLat?'結束取得座標':'手動取得座標'}}</button>

    <div class="mainInfoBox" :class="menuActive?'active':''">
      <div class="menuBtn" @click="menuActive?menuActive=false:menuActive=true" :class="menuActive?'active':''">
        <div></div>
        <div></div>
        <div></div>
      </div>
      <select style="width:100%;margin-bottom:10px;" :value="carTypeSelected" @change="carTypeChange">
        <option :value="key.type" v-for="(key, index) in carType" :key="index">
          {{ key.name }}
        </option>
      </select>

        
      <select style="width:calc(50% - 5px);margin-bottom:0;" :value="citySelected" @change="cityChange">
        <option :value="index" v-for="(key, index) in areaData" :key="index">
          {{ index }}
        </option>
      </select>
      <select style="width:calc(50% - 5px);margin-right:0" :value="cityAreaSelected" @change="cityAreaChange">
        <option value="">全選</option>
        <option :value="key" v-for="(key, index) in areaData[citySelected]" :key="index">
          {{ key }}
        </option>
      </select>


    </div>

    <div class="mainInfoBox mainInfoBox2" id="mainInfoBox2" :class="infoActive?'active':''">
      <div class="menuBtn" @click="infoActive=false" :style="infoActive?'':'display:none'" :class="infoActive?'active':''">
        <div></div>
        <div></div>
        <div></div>
      </div>
      <h3 id="parkingLotTitle"></h3>
      <div>地址: <div id="parkingLotAddress"></div></div>
      <div>營業時間: <div id="parkingLotBusinessHours"></div></div>
      <div id="parkingLotInfo"></div>

    </div>
    
    <pre id="coordinates" class="coordinates"></pre>

    <MapBox :carType="carTypeSelected" :getLngLat="getLngLat" :city="citySelected" :cityArea="cityAreaSelected" @parkingInfo="onSetParkingInfo" />
  </div>
</template>

<script>
import MapBox from "./components/MapBox.vue";
import "@/assets/css/style.css";
import AreaData from "@/assets/json/area_data.json";
// import axios from 'axios'; 

export default {
  name: "App",
  components: {
    MapBox,
  },
  data() {
    return {
      windowOpen: false,
      menuActive: true,
      infoActive: false,

      areaData : AreaData,
      citySelected: '台北市',
      cityAreaSelected: '',

      getLngLat: false,

      carTypeSelected: 1,
      carType: [
        { type: 1, name: "普通小型車" },
        { type: 2, name: "大型重型機車" },
        { type: 3, name: "普通重型機車" },
      ],
    };
  },
  created() {
    if (localStorage.getItem("userInitSetCarType")) {
      this.carTypeSelected = parseInt(
        localStorage.getItem("userInitSetCarType")
      );
    } else {
      this.windowOpen = true;
    }


  },
  mounted() {},
  methods: {
    carTypeChange(i) {
      this.carTypeSelected = parseInt(i.target.value);
    },
    cityChange(i){
      this.citySelected = i.target.value;
      // this.cityAreaSelected = this.areaData[this.citySelected][0];
      this.cityAreaSelected = "";
    },
    cityAreaChange(i){
      this.cityAreaSelected = i.target.value;
    },
    initCarTypeChange() {
      this.carTypeSelected = parseInt(
        document.getElementById("initSelectType").value
      );
      localStorage.setItem("userInitSetCarType", this.carTypeSelected);
      this.windowOpen = false;
    },

    onSetParkingInfo(data){
      document.getElementById('mainInfoBox2').scrollTop = 0;

      document.getElementById('parkingLotTitle').innerText = data.info;
      document.getElementById('parkingLotAddress').innerText = data.address;
      document.getElementById('parkingLotBusinessHours').innerText = data.times;
      const carInfo = data.carInfo;
      var html='<hr>';
      for(var key in carInfo){
        html = html + `
          <div class="carInfoBox">
            <h4> 
            ${key=='car'?'普通小型車':''}
            ${key=='superBike'?'大型重型機車':''}
            ${key=='scooter'?'普通重型機車':''}</h4> 
            <div>半小時 : ${carInfo[key].halfAnHour}</div>
            <div>1小時 : ${carInfo[key].anHour}</div>
            <div>單日上限 : ${carInfo[key].dayMax}</div>
          </div>
        `
      }
      document.getElementById('parkingLotInfo').innerHTML = html;
      this.infoActive = true;
    }
  },
};
</script>

